import { Component, OnDestroy, OnInit } from '@angular/core';
import {ShipmentService} from '../shipment.service';
import {Subscription} from 'rxjs';

@Component({
  selector: 'app-progress',
  templateUrl: './progress.component.html',
  styleUrls: ['./progress.component.scss']
})
export class ProgressComponent implements OnInit {

  public progressStatuses: any = [
      {method: 'Arrival Ort airport 12:00', address: '1000 W O Hare Ave, Chicago, IL 60666', expectedDate: '2020-01-25', actualDate: '2020-01-25', done: true},
      {method: 'Arrival Ort airport 12:00', address: '1000 W O Hare Ave, Chicago, IL 60666', expectedDate: '2020-01-25', actualDate: '2020-01-25', done: true},
      {method: 'Arrival Ort airport 12:00', address: '1000 W O Hare Ave, Chicago, IL 60666', expectedDate: '2020-01-25', actualDate: '2020-01-25', done: true},
      {method: 'Arrival Ort airport 12:00', address: '1000 W O Hare Ave, Chicago, IL 60666', expectedDate: '2020-01-25', actualDate: 'Nov 09, 2019', done: true}
      ];
  public statuses = [];
  subscription: Subscription;

  constructor(private shipmentService: ShipmentService) {
  }

  ngOnInit() {
    this.subscription = this.shipmentService.statusesSubject.subscribe((status: any) => {
      console.log(status)
      this.progressStatuses.push(status)
    })

  }

  ngOnDestroy() {
    this.subscription.unsubscribe()
  }
}
