import { Component, OnInit } from '@angular/core';
import {ShipmentService} from '../../shipment.service';

@Component({
  selector: 'app-progress-modal',
  templateUrl: './progress-modal.component.html',
  styleUrls: ['./progress-modal.component.scss']
})
export class ProgressModalComponent implements OnInit {

  public method = '';
  public address = '';
  public expectedDate = '';
  public actualDate = '';
  public done = false;
  constructor(private shipmentService: ShipmentService) { }

  ngOnInit() {
  }

  addNewStatus(event) {
    let invalid = false;

    if(!this.method.trim()) {
      invalid = true;
      document.getElementById('method').style.border = '2px solid red'
    } else{ document.getElementById('method').style.border = 'none' }
    if(!this.address.trim()) {
      invalid = true;
      document.getElementById('address').style.border = '2px solid red'
    } else{ document.getElementById('address').style.border = 'none' }
    if(!this.actualDate.trim()) {
      invalid = true;
      document.getElementById('actualDate').style.border = '2px solid red'
    } else{ document.getElementById('actualDate').style.border = 'none' }
    if(!this.expectedDate.trim()) {
      invalid = true;
      document.getElementById('expectedDate').style.border = '2px solid red'
    } else{ document.getElementById('expectedDate').style.border = 'none' }

    if(!invalid) {
      this.shipmentService.addNewStatus({
        method: this.method,
        address: this.address,
        expectedDate: this.expectedDate,
        actualDate: this.actualDate,
        done: this.done
      });
      this.method = '';
      this.address = '';
      this.actualDate = '';
      this.expectedDate = '';
      this.done = false;
      document.getElementById('cancel').click();
    }
  }
}
