import {Component, OnDestroy, OnInit} from '@angular/core';
import {ActivatedRoute, Params} from '@angular/router';
import {ShipmentService} from '../shipment.service';
import {Subscription} from 'rxjs';

@Component({
  selector: 'app-shipment-detail',
  templateUrl: './shipment-detail.component.html',
  styleUrls: ['./shipment-detail.component.scss']
})
export class ShipmentDetailComponent implements OnInit, OnDestroy {
  id: number;
  sub: Subscription;
  // TODO make an interface for shipment
  targetShipment: any;
  private addNotesMode: boolean;
  private editMode: boolean;

  constructor(private route: ActivatedRoute, private shipmentService: ShipmentService) { }

  ngOnInit() {
    this.editMode = false;
    this.addNotesMode = false;
    this.sub = this.route.params
        .subscribe((params: Params) => {
          this.id = +params['id'];
          this.targetShipment = this.shipmentService.getShipment(this.id);

    })
  }
  ngOnDestroy(): void {
    this.sub.unsubscribe();
  }

  onAddNotes() {
    this.addNotesMode = !this.addNotesMode
  }

  onEditShipment() {
    this.shipmentService.changeStatus('Update')
  }
}
