import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {AuthService} from "../auth.service";
import {Subject} from "rxjs";

@Injectable({
    providedIn: 'root'
})
export class ContactsService {


    constructor(private http: HttpClient, private auth: AuthService) { }

    public tableContact: any = new Subject();


    getContacts = () => {
        return this.auth.getContacts().subscribe(
            (data: any) => {
                console.log(data);
                this.tableContact.headerRow = [ 'Name', 'Company Name',  'Email', 'Phone', 'Quotes', 'Shipments', 'Action'];
                this.tableContact.dataRows = data.data;
                console.log(this.tableContact)
            },
            error => {
                console.log(error)
            }
        )
    }

    addContact(data) {
        console.log('function call')
        return this.auth.addContact(data).subscribe((data: any) => {
            console.log('res data ', data)
            let newContactData = this.tableContact;
            newContactData.dataRows = data.data;
            this.tableContact.next(newContactData);
        }, error => {
            console.log(error)
        })
    }


}
