import {Component, OnDestroy, OnInit} from '@angular/core';
import {ShipmentService} from '../shipment.service';
import {Subscription} from 'rxjs';

@Component({
  selector: 'app-modal-shipments',
  templateUrl: './modal-shipments.component.html',
  styleUrls: ['./modal-shipments.component.scss']
})
export class ModalShipmentsComponent implements OnInit, OnDestroy {
  status: string;
  subscription: Subscription
  constructor(private shipmentService: ShipmentService) { }

  ngOnInit() {

    this.subscription = this.shipmentService.statusSubject.subscribe((status: string) => {
      this.status = status
    })

  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe()
  }

}
