import {AfterViewInit, Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {ShipmentService} from '../shipment.service';

declare interface TableData {
  headerRow: string[];
  dataRows: string[][];
}

@Component({
  selector: 'app-shipment-list',
  templateUrl: './shipment-list.component.html',
  styleUrls: ['./shipment-list.component.scss']
})


export class ShipmentListComponent implements OnInit, AfterViewInit {

  public tableData1: TableData;
  currentPage = 1;
  itemsPerPage = 4;
  shipmentId: number;
  constructor( private router: Router, private shipmentService: ShipmentService) { }

  ngOnInit() {
    this.tableData1 = {
      // tslint:disable-next-line:max-line-length
      headerRow: [ 'Serial #', 'Date Created', 'Date Updated', 'Company Name', 'Model', 'Mode', 'Origin', 'Destination', 'Status', 'Action'],
      dataRows: [
        ['SN 134343', '02/11/2019', '02/11/2019', 'Oud-Turnhout', 'AW119', 'Ocean RORO', 'United States', 'Latvia', 'Completed'],
        ['SN 134348', '01/11/2019', '02/11/2019', 'Sinaai-Waas', 'AW119', 'Ocean RORO', 'United States', 'Latvia', 'Completed'],
        ['SN 134347', '03/11/2019', '02/11/2019', 'Baileux', 'AW119', 'Ocean RORO', 'United States', 'Latvia', 'Completed'],
        ['SN 134346', '04/11/2019', '02/11/2019', 'Overland Park', 'AW119', 'Ocean RORO', 'United States', 'Latvia', 'Completed'],
        ['SN 134345', '05/11/2019 ', '02/11/2019', 'Feldkirchen in Krnten', 'AW119', 'Ocean RORO', 'United States', 'Latvia', 'Completed'],
        ['SN 134344', '06/11/2019 ', '02/11/2019', 'Gloucester', 'AW119', 'Ocean RORO', 'United States', 'Latvia', 'Completed']
      ]
    };

  }
// add links and style to dynamic table of shipments

  initializeShipments() {
    const table = <HTMLTableElement> document.getElementById('tableId');
    // @ts-ignore
    for (const row of table.rows) {
      const firstCell = row.childNodes.item(1);
      firstCell.addEventListener('click', () => {
        this.router.navigate(['/shipments', this.shipmentId])
      });
      (<HTMLElement> firstCell).setAttribute('style', 'cursor: pointer; color: #2199e8');

    }
  }

  ngAfterViewInit(): void {
    this.initializeShipments()
  }

  addShipment() {
    console.log('add shipment method')
  }

  showId(i: number) {
    this.shipmentId = this.itemsPerPage * (this.currentPage - 1) + i;
    console.log( this.shipmentId)
  }

    setStatusToCreate() {
        this.shipmentService.changeStatus('Create')
    }
}
