import {Component, Input, OnInit} from '@angular/core';
import {CompaniesService} from "./companies.service";
import {Subject} from "rxjs";

declare interface TableData {
    headerRow: string[];
    dataRows: string[][];
}

@Component({
  selector: 'app-companies',
  templateUrl: './companies.component.html',
  styleUrls: ['./companies.component.css']
})
export class CompaniesComponent implements OnInit {
    companyName = '';
    primaryContact = '';
    email = '';
    phone = '';

    p: number = 1;
    public companyData = this.companyService.companyData;
    public companiesHeader = ['ID', 'Name', 'Company Name', 'Primary Contact', 'Email', 'Phone', 'Total Contacts', 'Quotes', 'Shipments', 'Action'];

    constructor(private companyService: CompaniesService) {
    }

    ngOnInit() {
        console.log(this.companyData)
        this.companyService.getCompanies()
    }

    deleteCompany(data) {
        this.companyService.deleteCompany(data.id)
    }

    editCompany(data) {
        this.companyName = data.companyName;
        this.primaryContact = data.primaryContact;
        this.email = data.email;
        this.phone = data.phone;
        this.companyService.openEditCompanyModal(data);
        document.getElementById('create_company').click();
    }

}
