import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {AuthService} from "../auth.service";
import {Subject} from "rxjs";

@Injectable({
    providedIn: 'root'
})
export class CompaniesService {


    constructor(private http: HttpClient, private auth: AuthService) {
    }

    public companyData: any = new Subject<{data: any}>();
    public isEdit = false;


    getCompanies() {
        return this.auth.getCompanies().subscribe(
            (data:any) => {
                console.log(data);
                this.companyData.data = data.data;
                console.log(this.companyData)
            },
            error => {
                console.log(error)
            }
        )
    }

    addCompany(data: any) {
        if(this.isEdit) {
            data.id = this.isEdit;
            data.name = 'Richard';
            data.totalContacts = 'test';
            data.quotes = 'test';
            data.shipments = 'shipments';
            return this.auth.editCompany(data, this.isEdit).subscribe((data:any) => {
                this.companyData.data = data.data;

            }, error => {
                console.log(error)
            })
        }
        return this.auth.addCompany(data).subscribe((data:any) => {
            this.companyData.data = data.data;

        }, error => {
            console.log(error)
        })
    }

    deleteCompany(id) {
        return this.auth.delCompany(id).subscribe((data: any) => {
            this.companyData.data = data.data;
        }, error => {
            console.log(error)
        })
    }

    openEditCompanyModal(data) {
        console.log(data);
        this.isEdit = data.id;
    }


}
